          </defs>
        </svg>
        <div class="container">
          <div class="checkout-addr">
            <div class="page-title-normal">
              <h2 class="page-title-h2"><span>核对地址信息</span></h2>
            </div>
            <!-- process step begin -->
            <div class="check-step">
              <ul>
                <li class="cur"><span>确认地址</span></li>
                <li><span>查看您的订单</span></li>
                <li><span>付款</span></li>
                <li><span>提交订单</span></li>
              </ul>
            </div>
            <!-- process step end -->
            <!-- address list begin -->
            <div class="page-title-normal checkout-title">
              <h2><span>配送地址</span></h2>
            </div>
            <div class="addr-list-wrap">
              <div class="addr-list">
                <ul>
                  <li v-for="(item,index) of addressList" :class="{'check': item.checked == 1}">
                    <dl @click="checkedAddress(item)">
                      <dt>{{item.peopleName}}</dt>
                      <dd class="address">{{item.address}}</dd>
                      <dd class="tel">{{item.phone}}</dd>
                    </dl>
                    <div class="addr-opration addr-del">
                      <a href="javascript:;" class="addr-del-btn">
                        <svg class="icon icon-del"><use xlink:href="#icon-del"></use></svg>
                      </a>
                    </div>
                    <div class="addr-opration addr-set-default" v-show="item.isDefault == 0">
                      <a href="javascript:;" class="addr-set-default-btn" @click.stop="_setDefaultAddress(item)"><i>设为默认地址</i></a>
                    </div>
                    <div class="addr-opration addr-default" v-show="item.isDefault != 0">默认地址</div>
                  </li>
                  <li class="addr-new">
                    <div class="add-new-inner" @click="_addAddress">
                      <i class="icon-add">
                        <svg class="icon icon-add"><use xlink:href="#icon-add"></use></svg>
                      </i>
                      <p>新增配送地址</p>
                    </div>
                  </li>
                </ul>
              </div>
              <!-- 显示更多 begin -->
              <div class="shipping-addr-more">
                <a v-show="showMore" class="addr-more-btn up-down-btn" href="javascript:;" @click="getMoreAddressList()">
                  more
                  <i class="i-up-down">
                    <i class="i-up-down-l"></i>
                    <i class="i-up-down-r"></i>
                  </i>
                </a>
              </div>
            </div>
            <!-- 显示更多 end -->
            <!-- address list end -->
            <!-- shipping method begin -->
            <div class="page-title-normal checkout-title">
              <h2><span>配送方式</span></h2>
            </div>
            <div class="shipping-method-wrap">
              <div class="shipping-method">
                <ul>
                  <li class="check">
                    <div class="name">配送标准</div>
                    <div class="price">免费</div>
                    <div class="shipping-tips">
                      <p>一旦商品发货，应在1 -7个工作日内到达目的地</p>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <!-- shipping method end -->
            <div class="next-btn-wrap">
              <a class="btn btn--m btn--red" href="javascript:;" @click="next">下一步</a>
            </div>
          </div>
        </div>
      </div>
      <pop :text="popText" :showDialog="showPop" @closePop="closeChildDialog"></pop>
      <add-address :showAddressForm="showFormAddress" @closeAddress="closeAddressDialog"></add-address>
      <nav-footer></nav-footer>
    </div>
</template>

<script>
import NavHeader from '@/components/Header.vue'
import NavFooter from '@/components/Footer.vue'
import NavBread from '@/components/NavBread.vue'
import addAddress from 'base/addAddress.vue'
import {getCookie} from 'util/util'
import Pop from 'base/pop'
import {getAddressList, setDefaultAddress, selectAddress} from 'api/addressList'
import {ERR_OK} from 'api/config'

export default{
    data(){
        return{
            popText: '',
            showPop: false,
            showFormAddress: false,
            addressList: [],
            showMore: true,
            userId: ''
        }
    },
    mounted() {
        this._getAddressList(0)
    },
    methods: {
        next() {
            // 设当前选中数字为0；
            let activeSelect = 0;
            this.addressList.forEach((item) => {
                if(item.checked == 1) {
                    activeSelect++;
                }
            });
            if(activeSelect == 0) {
                this.popText = "请选择一个配送地址";
                this.showPop = true;
                return
            }
            this.$router.push({
                path:'/orderConfirm'
            })
        },
        // 获取地址列表
        _getAddressList(loadMore) {
            this.userId = getCookie('user_uid');
            getAddressList(this.userId,loadMore).then((res) => {
                if(res.code == ERR_OK) {
                    this.addressList = res.data
                }
            })
        },
        // 新增地址
        _addAddress() {
            this.showFormAddress = true
        },
        // 关闭地址弹窗
        closeAddressDialog() {
            this._getAddressList(1);
            this.showFormAddress = false
        },
        // 设为默认地址
        _setDefaultAddress(item) {
            setDefaultAddress(this.userId,item.aid).then((res) => {
                if(res.code == ERR_OK) {
                    this._getAddressList(1)
                }
            }) 
        },
        // 选中当前地址为配送地址
        checkedAddress(item) {
            selectAddress(this.userId,item.aid).then((res) => {
                if(res.code == ERR_OK) {
                    this._getAddressList(1)
                }
            }) 
        },
        // 显示更多
        getMoreAddressList() {
            this._getAddressList(1);
            this.showMore = false
        },
        // 关闭信息弹窗
        closeChildDialog() {
            this.showPop = false
        }
    },
    components: {
        NavHeader,
        NavFooter,
        NavBread,
        Pop,
        addAddress
    }
}
</script>

<style>
.addr-list li .addr-set-default {
    display: block;
}
</style>



